<template>
    <div>
        <div class="banner"  @click="handleGallarOpen">
            <img class="banner-img" :src="bannerImg" alt="大连圣亚海洋世界">
            <div class="banner-info">
                <div class="banner-title">{{this.sightName}}</div>
                <div class="banner-number">
                    <span class="iconfont banner-icon">&#xe692;</span>
                    {{this.bannerImgs.length}}
                </div>
            </div>
        </div>
        <fade-animation>
            <common-gallary
                :imgs="bannerImgs" @close="handleGallaryClose"
                v-show="isGallaryShow"
            ></common-gallary>
        </fade-animation>
    </div>
</template>

<script>
    import CommonGallary from 'common/gallary/Gallary'
    import FadeAnimation from 'common/fade/Fade'
    export default {
        name: "DetailBanner",
        props: {
            bannerImg: String,
            sightName: String,
            bannerImgs: Array
        },
        components: {
            CommonGallary,
            FadeAnimation
        },
        data () {
            return {
                isGallaryShow: false
            }
        },
        methods : {

            handleGallaryClose () {
                this.isGallaryShow = false
            },
            handleGallarOpen () {
                this.isGallaryShow = true
            }
        }
    }
</script>

<style lang="stylus" scoped>
.banner
    position: relative
    overflow: hidden
    height: 0
    padding-bottom: 55%
    .banner-img
        width: 100%
    .banner-info
        display: flex
        position: absolute
        left: 0
        right: 0
        bottom: 0
        line-height: .6rem
        color: #fff
        background-image: linear-gradient(top, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8))
        .banner-tittle
            flex: 1
            font-size: .32rem
            padding: 0 .2rem
        .banner-number
            height: .32rem
            line-height: .32rem
            margin-top: .14rem
            padding: 0 .4rem
            border-radius: .2rem
            background: rgba(0, 0, 0, .8)
            font-size: .24rem
            .banner-icon
                font-size: .24rem
</style>


